var foodsWeKnow = Object.keys(foodDictionary);
// var likeCounter = 0;
export default function App() {
  const [food, setFood] = useState("");
  function foodInputHandler(event) {
    var userInput = event.target.value;
    var foodIdentified = foodDictionary[userInput];
    if (foodIdentified === undefined) {
      foodIdentified = "we don't have this in our database";
    }
    setFood(foodIdentified);
  }

  function foodClickHandler(food) {
    var foodIdentified = foodDictionary[food];
    setFood(foodIdentified);
  }
  return (
    <div className="App">
      <h1>{headingText}</h1>
      <input onChange={foodInputHandler} />
      <h2> {food} </h2>
      <h3>Foods We Know</h3>
      {foodsWeKnow.map((food) => {
        return (
          <span
            onClick={() => foodClickHandler(food)}
            style={{
              padding: "0.5rem",
              fontSize: "1.25rem",
              cursor: "pointer"
            }}
          >
            {" "}
            {food}{" "}
          </span>
        );
      })}
    </div>
  );
}

/*
VISER - 
View → Interact → 
State in Event Handler → Render/React
*/
